Object.freeze(ContentItemType);

/**
 * Return ContentItemType for a given MediaType.
 * @param type {MediaType}
 * @returns {ContentItemType}
 */
export function bscGetContentItemTypeForMediaType(type: MediaType): ContentItemType {
  if (type !== MediaType.Other ) {
    // Take advantage of the fact that ContentItemStrings are equivalent to MediaType strings for media content
    return type as ContentItemType;
  }
  return ContentItemType.Invalid;
}

