export class PlayerModel {
  // Impala
  static XD1033 = 'XD1033';
  static XD233 = 'XD233';
  static XT1143 = 'XT1143';
  static XT243 = 'XT243';

  // Pantera
  static HD1023 = 'HD1023';
  static HD223 = 'HD223';
  static HS123 = 'HS123';
  static LS423 = 'LS423';

  // Tiger
  static FK1142 = '4K1142';
  static FK1042 = '4K1042';
  static FK242 = '4K242';

  // Lynx
  static XD1132 = 'XD1132';
  static XD1032 = 'XD1032';
  static XD232 = 'XD232';

  // Bobcat
  static HD1022 = 'HD1022';
  static HD222 = 'HD222';
  static HD972 = 'HD972';
  static HD922 = 'HD922';

  // Cheetah
  static XD1230 = 'XD1230';
  static XD1030 = 'XD1030';
  static XD230 = 'XD230';

  // Panther
  static HD1020 = 'HD1020';
  static HD220 = 'HD220';
  static HD120 = 'HD120';
  static LS422 = 'LS422';
  static LS322 = 'LS322';
  static AU320 = 'AU320';

  // Puma
  static HD970 = 'HD970';
  static HD920 = 'HD920';
  static A915 = 'A915';
  static HD917 = 'HD917';
}

const ImpalaModelArray: PlayerModel[] = [
  PlayerModel.XD1033,
  PlayerModel.XD233,
  PlayerModel.XT1143,
  PlayerModel.XT243,
];
export const ImpalaModels: Set<PlayerModel> = new Set(ImpalaModelArray);

const PanteraModelArray: PlayerModel[] = [
  PlayerModel.HD1023,
  PlayerModel.HD223,
  PlayerModel.HS123,
  PlayerModel.LS423,
];
export const PanteraModels: Set<PlayerModel> = new Set(PanteraModelArray);

const TigerModelArray: PlayerModel[] = [
  PlayerModel.FK1142,
  PlayerModel.FK1042,
  PlayerModel.FK242,
];
export const TigerModels: Set<PlayerModel> = new Set(TigerModelArray);

const LynxModelArray: PlayerModel[] = [
  PlayerModel.XD1132,
  PlayerModel.XD1032,
  PlayerModel.XD232,
];
export const LynxModels: Set<PlayerModel> = new Set(LynxModelArray);

const BobcatModelArray: PlayerModel[] = [
  PlayerModel.HD1022,
  PlayerModel.HD222,
  PlayerModel.HD972,
  PlayerModel.HD922,
];
export const BobcatModels: Set<PlayerModel> = new Set(BobcatModelArray);

const CheetahModelArray: PlayerModel[] = [
  PlayerModel.XD1230,
  PlayerModel.XD1030,
  PlayerModel.XD230,
];
export const CheetahModels: Set<PlayerModel> = new Set(CheetahModelArray);

const PantherModelArray: PlayerModel[] = [
  PlayerModel.HD1020,
  PlayerModel.HD220,
  PlayerModel.HD120,
  PlayerModel.LS422,
  PlayerModel.LS322,
  PlayerModel.AU320,
];
export const PantherModels: Set<PlayerModel> = new Set(PantherModelArray);

const PumaModelArray: PlayerModel[] = [
  PlayerModel.HD970,
  PlayerModel.HD920,
  PlayerModel.A915,
  PlayerModel.HD917,
];
export const PumaModels: Set<PlayerModel> = new Set(PumaModelArray);

export const Series3Models: Set<PlayerModel> = new Set(
  ImpalaModelArray.concat(PanteraModelArray),
);
export const Series2Models: Set<PlayerModel> = new Set(
  TigerModelArray.concat(LynxModelArray, BobcatModelArray),
);
export const AllModels: Set<PlayerModel> = new Set(
  ImpalaModelArray.concat(
    PanteraModelArray,
    TigerModelArray,
    LynxModelArray,
    BobcatModelArray,
    CheetahModelArray,
    PantherModelArray,
    PumaModelArray,
  ),
);



