/**
 * @const AllVideoConnectorTypeSpec {VideoConnectorType[]}
 * Array of (@link VideoConnectorType}
 */
export const AllVideoConnectorTypeSpec: VideoConnectorType[] = [
  VideoConnectorType.Hdmi,
  VideoConnectorType.Vga,
  VideoConnectorType.Component,
];

/**
 * @const HdmiVideoConnectorTypeSpec {VideoConnectorType[]}
 * Array of (@link VideoConnectorType}
 */
export const HdmiVideoConnectorTypeSpec: VideoConnectorType[] = [
  VideoConnectorType.Hdmi,
];

/**
 * Get VideoConnectorType array for given playerModel.
 *
 * @param model {PlayerModel}
 * @returns {VideoConnectorType[] | null} return null if model is invalid or has no connectors
 */
export function bscGetPlayerVideoConnectorTypes(
  model: PlayerModel,
): VideoConnectorType[] | null {
  switch (model) {
    case PlayerModel.HD120:
    case PlayerModel.HD220:
    case PlayerModel.HD1020:
    case PlayerModel.XD230:
    case PlayerModel.XD1030:
    case PlayerModel.XD1230:
    case PlayerModel.HD920:
    case PlayerModel.HD970:
    case PlayerModel.HD922:
    case PlayerModel.HD972:
    case PlayerModel.HD222:
    case PlayerModel.HD1022:
    case PlayerModel.XD232:
    case PlayerModel.XD1032:
    case PlayerModel.XD1132:
      return AllVideoConnectorTypeSpec;
    case PlayerModel.HD1023:
    case PlayerModel.HS123:
    case PlayerModel.HD223:
    case PlayerModel.XD1033:
    case PlayerModel.XD233:
    case PlayerModel.XT1143:
    case PlayerModel.XT243:
    case PlayerModel.LS423:
    case PlayerModel.A915:
    case PlayerModel.HD917:
    case PlayerModel.FK242:
    case PlayerModel.FK1042:
    case PlayerModel.FK1142:
    case PlayerModel.LS422:
      return HdmiVideoConnectorTypeSpec;
    default:
      return null;
  }
}

/**
 * Return true if given connector type is valid for given playerModel.
 *
 * @param model {PlayerModel}
 * @param connectorType {VideoConnectorType}
 * @returns {boolean}
 */
export function bscIsValidConnectorType(
  model: PlayerModel,
  connectorType: VideoConnectorType,
): boolean {
  const validConnectorTypes = bscGetPlayerVideoConnectorTypes(model);
  if (validConnectorTypes !== null) {
    return validConnectorTypes.indexOf(connectorType) > -1;
  } else {
    return false;
  }
}

/**
 * Get PlayerVideoModeSpec array for given playerModel and videoConnectorType.
 *
 * @param model {PlayerModel}
 * @param connectorType {VideoConnectorType}
 * @returns {PlayerVideoModeSpec | null} return null if model or connectorType is invalid or not supported
 */
export function bscGetPlayerVideoModeSpecs(
  model: PlayerModel,
  connectorType: VideoConnectorType,
): PlayerVideoModeSpec | null {
  switch (true) {
    case ImpalaModels.has(model):
      return ImpalaVideoModeSpec;
    case PanteraModels.has(model):
      return PanteraVideoModeSpec;
    case TigerModels.has(model):
      return TigerVideoModeSpec;
    case LynxModels.has(model): {
      switch (connectorType) {
        case VideoConnectorType.Hdmi: return LynxHdmiVideoModeSpec;
        case VideoConnectorType.Vga: return LynxVgaVideoModeSpec;
        case VideoConnectorType.Component: return LynxComponentVideoModeSpec;
        default: return null;
      }
    }
    case BobcatModels.has(model): {
      switch (connectorType) {
        case VideoConnectorType.Hdmi: return BobcatHdmiVideoModeSpec;
        case VideoConnectorType.Vga: return BobcatVgaVideoModeSpec;
        case VideoConnectorType.Component: return BobcatComponentVideoModeSpec;
        default: return null;
      }
    }
    default:
      return null;
  }
}

/**
 * Get array of available frame rates for given playerModel, videoConnectorType, and videoResolution.
 *
 * @param model {PlayerModel}
 * @param connectorType {VideoConnectorType}
 * @param resolution {VideoResolution}
 * @returns {VideoFrameRate[]} return null if model, connectorType, or resolution is invalid or not supported
 */
export function bscGetFrameRates(
  model: PlayerModel,
  connectorType: VideoConnectorType,
  resolution: VideoResolution,
): VideoFrameRate[] | null {
  const videoModeOptions = bscGetPlayerVideoModeSpecs(model, connectorType);
  if (videoModeOptions !== null) {
    for (const videoModeOption of videoModeOptions){
      if (resolution === videoModeOption.resolution) {
        return videoModeOption.availableRates;
      }
    }
  }
  return null;
}

/**
 * Return true if given resolution is valid for given playerModel and videoConnectorType.
 * @param model {PlayerModel}
 * @param connectorType {VideoConnectorType}
 * @param resolution {VideoResolution}
 * @returns {boolean}
 */
export function bscIsValidResolution(
  model: PlayerModel,
  connectorType: VideoConnectorType,
  resolution: VideoResolution,
): boolean {
  return bscGetFrameRates(model, connectorType, resolution) !== null;
}

/**
 * Return true if given frame rate is valid  for given playerModel, videoConnectorType, and videoResolution.
 *
 * @param model {PlayerModel}
 * @param connectorType {VideoConnectorType}
 * @param resolution {VideoResolution}
 * @param frameRate {VideoFrameRate}
 * @returns {boolean}
 */
export function bscIsValidFrameRate(
  model: PlayerModel,
  connectorType: VideoConnectorType,
  resolution: VideoResolution,
  frameRate: VideoFrameRate,
): boolean {
  const validFrameRates = bscGetFrameRates(model, connectorType, resolution);
  if (validFrameRates !== null) {
    return validFrameRates.indexOf(frameRate) > -1;
  } else {
    return false;
  }
}

/**
 * @interface VideoModeComponents - video attributes (size, frame rate, interlaced) expressed as scalar data types.
 *  @property width {number} - vide width in pixels
 *  @property height {number} - video height in pixels
 *  @property frameRate {number} - video frame rate in frames or fields per second
 *  @property interlaced {boolean} - true if video consists of interlaced fields
 */
export interface VideoModeComponents {
  width: number;
  height: number;
  frameRate: number;
  interlaced: boolean;
}

/**
 * Return video attributes (size, frame rate, interlaced) for a given VideoMode expressed
 *  as scalar data types.
 *
 * @param mode (VideoMode)
 * @returns {VideoModeComponents} - video attributes for the given mode
 */
export function bscParseVideoModeComponents(mode: VideoMode): VideoModeComponents {
  const components: VideoModeComponents = { width: 0, height: 0, frameRate: 0, interlaced: false };
  const matchResult = (mode as string).match(/(\d*)x(\d*)x(.*|.*\..*)([ip])/);
  if (matchResult != null && matchResult.length === 5) {
    components.width = Number(matchResult[1]);
    components.height = Number(matchResult[2]);
    components.frameRate = Number(matchResult[3]);
    components.interlaced = matchResult[4] === 'i';
  } else if ((mode as string).match(/NTSC/) || (mode as string).match(/PAL-M/)) {
    components.width = 720;
    components.height = 480;
    components.frameRate = 59.94;
    components.interlaced = true;
  } else if ((mode as string).match(/PAL/) || (mode as string).match(/SECAM/)) {
    components.width = 720;
    components.height = 576;
    components.frameRate = 50;
    components.interlaced = true;
  }
  return components;
}

/**
 * @interface VideoModeResolutionAndRate - simple object to return resolution and frame rate
 *
 * @property resolution {VideoResolution}
 * @property frameRate {VideoFrameRate}
 */
export interface VideoModeResolutionAndRate {
  resolution: VideoResolution;
  frameRate: VideoFrameRate;
}

/**
 * Return separate VideoResolution and VideoFrameRate for given VideoMode.
 *
 * @param mode (VideoMode)
 * @returns {VideoModeResolutionAndRate | null} return null if videoMode is invalid or not supported
 */
export function bscParseVideoModeResolutionAndRate(mode: VideoMode): VideoModeResolutionAndRate | null {
  const result: VideoModeResolutionAndRate = {
    resolution: VideoResolution.v1920x1080,
    frameRate: VideoFrameRate.r60p,
  };
  const matchResult = (mode as string).match(/(\d*x\d*)x(.*|.*\..*[ip])/);
  if (matchResult != null && matchResult.length === 3) {
    result.resolution = matchResult[1] as VideoResolution;
    result.frameRate = matchResult[2] as VideoFrameRate;
  } else if ((mode as string).match(/NTSC/) || (mode as string).match(/PAL-M/)) {
    result.resolution = mode;
    result.frameRate = VideoFrameRate.r59i;
  } else if ((mode as string).match(/PAL/) || (mode as string).match(/SECAM/)) {
    result.resolution = mode;
    result.frameRate = VideoFrameRate.r50i;
  } else {
    return null;
  }
  return result;
}



